.wrapper {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
  background: rgba(0, 0, 0, 0.6);
}

.AuthForm {
  display: flex;
  align-items: center;
  background-color: #FFFFFF;
  border-radius: 1em;
}

.AuthForm__container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  gap: 1em;

}

a {
  text-decoration: none;
}

h2 {
  text-align: center;
}



form {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  gap: 1em;
  width: 100%;
}

.AuthForm input {
  border: none;
  display: block;
  width: 100%;
  border-bottom: 2px solid #E4E4E4;
}


.card {
  display: flex;
  column-gap: 10px;
}

.card__mm {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.text {
  text-align: center;
}




/*  */

.profile-section {
  height: calc(100vh - 72px);
  position: relative;
  pointer-events: none;
}

.profile-section__wrapper {
  pointer-events: all;
}

.profile-wrapper {
  position: absolute;
  background-color: rgba(28, 26, 25);
  opacity: 0.5;
  inset: 0px;
}

.profile {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 60px 43px;
  border-radius: 10px;
  background: white;

}

.profile__title {
  font-size: 36px;
  font-weight: bold;
  text-align: center;
}

.profile__subtitle {
  text-align: center;
}

.profile__data {
  display: flex;
  align-items: center;
  padding: 40px 0;
}

.profile__column {
  flex: 1;
  margin-right: 100px;
}

.profile__column:last-child {
  margin-right: 0;
}

.profile__form {
  display: flex;
  flex-direction: column;
}

.form__row {
  display: flex;
}

.form__row .label {
  font-weight: normal;
}

.profile__button {
  display: flex;
  justify-content: center;
}

.btn__color {
  color: #000;
}

.profile__card {
  width: 347px;
  height: 182px;
  display: flex;
  padding: 18px 28px;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0px 5px 20px 2px rgb(0 0 0 / 10%);
}

.card__row {
  display: flex;
  justify-content: space-between;
}

.card__row:first-child {
  font-size: 16px;
}

.card__row:nth-child(2) {
  font-size: 21px;
}

.circle {
  width: 38px;
  height: 28px;
  display: flex;
}

.circle::before,
.circle::after {
  content: '';
  width: 28px;
  height: 28px;
  display: flex;
  flex-shrink: 0;
  border-radius: 50%;
  mix-blend-mode: multiply;
  background: #f1f1f1;
}

.circle::after {
  margin-left: -10px;
}